<template>
  <div class="commonModel resetAppSecret">
    <a-modal
      title="Reset App secret"
      v-model="visible"
      @ok="hideModal"
      :footer="null"
      class="noformModel"
      :closable="false"
      :destroyOnClose="true"
    >
      <div class="appSecret">
        <div >
          <!-- <span>
            <span><i class="iconfont amd-star"></i></span>
            <span id="secret">{{appSecret}}</span>
          </span> -->
          <span class="star"><i class="iconfont amd-star"></i></span>
          <span id="secret">{{appSecret}}</span>
          <button type="button" v-clipboard:copy="appSecret" v-clipboard:success="onCopy" v-clipboard:error="onError">copy</button>
        </div>
        <p>Please keep in mind the app secret, the system does not provide query service, if you forget to only regenerate!</p>
        <p class="modelBtn">
          <a-button type="primary" @click="hideModal">Confirm</a-button>
        </p>
      </div>
    </a-modal>
  </div>
</template>
<script>
import Cookies from "@/js/cookies";

export default {
  data() {
    return {
      visible: false,
      appSecret: "",
      appId: Cookies.get("appId"),
      app_key: Cookies.get("appKey")
    };
  },
  inject: ["reload"],
  computed: {},
  methods: {
    showModal() {
      // this.getAppSecret();
      this.getAppSecret();
      if (this.appSecret) {
        this.visible = true;
      }
    },
    hideModal() {
      this.visible = false;
    },
    getAppSecret() {
      let values = {
        app_id: this.appId,
        app_key: this.app_key
      };
      console.log(values);
      this.$store.dispatch("getAppSecret", values).then(
        res => {
          console.log(res);
          this.appSecret = res.data.secret;
        },
        error => {
          console.log(error);
        }
      );
    },
    onCopy(){
        this.$message.info('复制成功');
    },
    onError(){
        this.$message.error('复制失败');        
    }
  },
  created() {
    this.getAppSecret();
  }
};
</script>

<style lang="less">
@import "../../css/app.less";
</style>


