
<template>
  <div class="showContent appContent">
    <backSubMenu :backTitle="backTitle" :lineUnder="true"></backSubMenu>
    <div class="box">
      <div class="iconAndName">
        <div>
          <img :src="app.icon" alt>
        </div>
        <div>
          <p>{{app.name}}</p>
          <p>
            <span>App ID:</span>
            {{app._id}}
          </p>
          <p class="secret">
            <span>App Secret:</span>******
            <span @click="resetAppSecret">Reset</span>
          </p>
        </div>
      </div>
      <div class="baseInfor">
        <p>Basic Information</p>
        <div class="baseInforConent">
          <div>
            <div>App name</div>
            <div>{{app.name}}</div>
          </div>
          <div>
            <div>Business label</div>
            <div class="tagP"><span v-for="(tag,index) in app.tag" :key="index">{{tag}}</span></div>
          </div>
          <div>
            <div>Platform Introduction</div>
            <div>{{app.description}}</div>
          </div>
          <div>
            <div>Official website</div>
            <div>{{app.website}}</div>
          </div>
          <div>
            <div>App image</div>
            <div><div id="appIcon"><img :src="app.icon" alt></div></div>
          </div>
        </div>
      </div>
      <div class="baseInfor">
        <p class="baseInforP2">Application configuration</p>
        <div class="baseInforConent">
          <div>
            <div>Message verification Token</div>
            <div>{{app.message_check_token}}</div>
          </div>
          <div>
            <div>Message encryption key</div>
            <div>{{app.message_encryption}}</div>
          </div>
           <div>
            <div>Callback URL</div>
            <div>{{app.callback}}</div>
          </div>
          <div>
            <div>White list</div>
            <!-- <div>{{app.white_list.join(";")}}</div> -->
            <div class="tagP"><span v-for="(tag,index) in app.white_list" :key="index">{{tag}}</span></div>
          </div>
        </div>
      </div>
      <p class="edit">
        <a-button type="primary" @click="gotoEdit">修改</a-button>
      </p>
    </div>
    <appSecret ref="appSecretRef"></appSecret>
  </div>
</template>
<script>
import Cookies from "@/js/cookies";
import appSecret from "@/page/app/resetAppSecretModel";
import backSubMenu from "@/components/backSubMenu";
export default {
  data() {
    return {
      appId: Cookies.get("appId"),
      app_key: Cookies.get("appKey"),
      app: "",
      backTitle: "APP Detail"
    };
  },
  components: {
    backSubMenu,
    appSecret
  },
  methods: {
    // 获取appdetail
    getAppDetail() {
      let val = {
        app_id: this.appId,
        app_key: this.app_key
      };
      console.log(val);
      this.$store.dispatch("getAppDetail", val).then(
        res => {
          console.log(res);
          let appdata = res.data;
          appdata.icon="http://kotadata.oss-ap-southeast-5.aliyuncs.com"+appdata.icon;
          this.app = { ...appdata };
          console.log(this.app);
        },
        error => {
          console.log(error);
        }
      );
    },
    handleTableChange(pagination) {
      this.getApplist(pagination.current);
    },
    back() {
      this.$router.push({ name: "applist" });
    },
    resetAppSecret() {
      this.$refs.appSecretRef.showModal();
    },
    gotoEdit() {
      this.$router.push({ name: "editApp", params: { app: this.app } });
    }
  },
  created() {
    this.getAppDetail();
  }
};
</script>

<style lang="less">
@import "../../css/app.less";
</style>


